.click-item {
    background-size: cover;
    background-position: 50%;
    height: 170px;
    width: 170px;
    margin: 8px 25px;
    border: 5px solid #f1f1f1;
    border-radius: 3px;
    box-shadow: 0 3px 6px rgba(0,0,0,.16), 0 3px 6px rgba(0,0,0,.23);
    cursor: pointer;
}
.footer {
    background: red;
    height: 100px;
    position: relative;
    color: #fff;
    overflow: auto;
}

.footer-bottom {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    background:black;
    padding: 8px;
    font-weight: 600;
}

.footer img {
    width: 20px;
    position: relative;
    top: 3px;
    left: 2px;
}
.content {
    display: -webkit-flex;
    display: flex;
    min-height: 70%;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: center;
            justify-content: center;
    padding-top: 50px;
    padding-bottom: 50px;
    width: 90%;
    margin: 0 auto;
  }
  
  .App {
    min-width: 350px;
  }
